.classcontainer {
  display: flex;
  height: 50vh;
}

.destrocontainer {
  display: flex;
  height: 50vh;
}

.demosection {
  background-image: url(../Imagenes/demo.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  -moz-box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  -webkit-box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  transition: filter 0.2s ease-in-out;
  -webkit-filter: grayscale(0%);
  filter: grayscale(100%);
  cursor: pointer;
}
.demosection:hover {
  -webkit-filter: grayscale(100%);
  filter: grayscale(0%);
}

.afflicsection {
  background-image: url(../Imagenes/aff.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  -moz-box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  -webkit-box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  transition: filter 0.2s ease-in-out;
  -webkit-filter: grayscale(0%);
  filter: grayscale(100%);
  cursor: pointer;
}

.afflicsection:hover {
  -webkit-filter: grayscale(100%);
  filter: grayscale(0%);
}

.destrosection {
  background-image: url(../Imagenes/destro.jpg);
  background-repeat: no-repeat;
  background-position: 10% 30%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  -moz-box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  -webkit-box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  transition: filter 0.2s ease-in-out;
  -webkit-filter: grayscale(0%);
  filter: grayscale(100%);
  cursor: pointer;
}

.destrosection:hover {
  -webkit-filter: grayscale(100%);
  filter: grayscale(0%);
}

.classcontainer h1 {
  color: white;
  margin-left: 50px;
  font-size: 3em;
  text-shadow: 2px 2px #000000;
}

.destrocontainer h1 {
  color: white;
  margin-left: 50px;
  font-size: 3em;
  text-shadow: 2px 2px #000000;
}

.dungeoncontainer {
  display: flex;
  height: 49vh;
}

.nyalothacontainer {
  display: flex;
  height: 50vh;
}

.mythicsection {
  background-image: url(../Imagenes/dungeon.jpg);
  background-position: 60% 95%;
  background-size: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  -moz-box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  -webkit-box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  transition: filter 0.5s ease-in-out;
  -webkit-filter: brightness(0%);
  filter: brightness(100%);
  cursor: pointer;
}

.mythicsection:hover {
  -webkit-filter: brightness(0%);
  filter: brightness(130%);
}

.mythicsection h1 {
  color: white;
  margin-left: 50px;
  margin-top: 0%;
  margin-bottom: 0%;
  font-size: 3em;
  text-shadow: 2px 2px #000000;
}

.visionsection {
  background-image: url(../Imagenes/visionnew.jpg);
  background-position: 60% 95%;
  background-size: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  -moz-box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  -webkit-box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  transition: filter 0.5s ease-in-out;
  -webkit-filter: brightness(0%);
  filter: brightness(100%);
  cursor: pointer;
}

.visionsection:hover {
  -webkit-filter: brightness(0%);
  filter: brightness(130%);
}

.visionsection h1 {
  color: white;
  margin-left: 50px;
  margin-top: 0%;
  margin-bottom: 0%;
  font-size: 3em;
  text-shadow: 2px 2px #000000;
}

.nyalothasection {
  background-image: url(../Imagenes/nyalotha.jpg);
  background-repeat: no-repeat;
  background-position: 10% 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  -moz-box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  -webkit-box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  box-shadow: 3px 3px 5px 6px rgb(0, 0, 0);
  transition: filter 0.5s ease-in-out;
  -webkit-filter: brightness(0%);
  filter: brightness(100%);
  cursor: pointer;
}

.nyalothasection:hover {
  -webkit-filter: brightness(0%);
  filter: brightness(130%);
}

.nyalothasection h1 {
  color: white;
  margin-left: 50px;
  margin-top: 0%;
  margin-bottom: 0%;
  font-size: 3em;
  text-shadow: 2px 2px #000000;
}

.nyalothafade {
  margin-top: 0%;
  margin-left: 50px;
  color: white;
  font-size: 1.3em;
  text-shadow: 2px 2px #000000;
  display: none;
}

#parent:hover .nyalothafade {
  animation: fadeIn ease 1s;
  display: block;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-50px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
}

.mythicfade {
  margin-top: 0%;
  margin-left: 50px;
  color: white;
  font-size: 1.3em;
  text-shadow: 2px 2px #000000;
  display: none;
}

#parent2:hover .mythicfade {
  animation: fadeInMythic ease 1s;
  display: block;
}

@keyframes fadeInMythic {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-50px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
}

.horrific {
  overflow: auto;
  color: white;
  margin-top: 0%;
  margin-left: 50px;
  font-size: 1.3em;
  text-shadow: 2px 2px #000000;
  display: none;
}

#horri:hover .horrific {
  animation: fadeInMythic ease 1s;
  display: block;
}

@keyframes fadeInMythic {
  0% {
    opacity: 0;
    -webkit-transform: translateX(50px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
}

.main {
  display: flex;
  flex-direction: column;
}

@media (min-width: 375px) and (max-width: 480px) {
  .classcontainer {
    display: flex;
    height: 25%;
    flex-direction: column;
  }

  .demosection {
    width: 100%;
  }

  .afflicsection {
    width: 100%;
  }

  .destrocontainer {
    display: flex;
    height: 25%;
  }

  .dungeoncontainer {
    display: flex;
    height: 25%;
  }

  .nyalothacontainer {
    display: flex;
    height: 25%;
  }

  .classcontainer h1 {
    color: white;
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: 0px;
    font-size: 3em;
    text-shadow: 2px 2px #000000;
  }

  .visionsection h1 {
    margin-left: 0px;
  }
}
